<template>
     <main class="container">
      <article class="grid">
        <div>
          <hgroup>
            <h1>add a secret</h1>
          </hgroup>
          <form @submit=addSecret>
            <input
              type="text"
              name="secret"
              v-model="secret"
              placeholder="secret"
              aria-label="secret"
              required
            />
            <button type="submit" class="contrast">add</button>
          </form>
        </div>
        <div></div>
      </article>
    </main>
</template>

<script>
  import axios from 'axios';

  export default {
        methods: {
            async addSecret(event) {
                event.preventDefault();
                try {
                    await axios.put('/me/secret', {
                        secret: this.secret,
                      }, {
                          headers: {Authorization: `Bearer ${localStorage.token}`}
                      });
                    

                    this.$router.push({name: 'me'});
                } catch (error) {
                  console.log(error);
                  this.$router.push({
                    name : "error",
                    query: { error: JSON.stringify(error.response.data.message) },
                    });
                }
            },
        },
        data: function () {
            return {
                secret: null,
            };
        },

  }
</script>
