<template>
     <main class="container">
      <article class="grid">
        <div>
          <hgroup>
            <h1>login</h1>
            <h2>login with your company</h2>
          </hgroup>
          <form @submit=register>
            <input
              type="text"
              name="login"
              v-model="login"
              placeholder="login"
              aria-label="login"
              autocomplete="nickname"
              required
            />
            <input
              type="text"
              name="companyName"
              v-model="companyName"
              placeholder="company name"
              aria-label="company name"
              autocomplete="nickname"
              required
            />
            <input
              type="password"
              name="password"
              v-model="password"
              placeholder="password"
              aria-label="password"
              autocomplete="current-password"
              required
            />
            <button type="submit" class="contrast">Login</button>
          </form>
        </div>
        <div></div>
      </article>
    </main>
</template>

<script>
  import axios from 'axios';

  export default {
        methods: {
            async register(event) {
                event.preventDefault();
                try {
                    let resp = await axios.post('register', {
                        login: this.login,
                        name: this.companyName,
                        password: this.password,
                    });
                    
                    localStorage.token = resp.data.message.jwt;

                    this.$router.push({name: 'index'});
                } catch (error) {
                  console.log(error);
                  this.$router.push({
                    name : "error",
                    query: { error: JSON.stringify(error.response.data.message) },
                    });
                }
            },
        },
        data: function () {
            return {
                login: null,
                companyName: null,
                password: null,
            };
        },

  }
</script>
