<template>
  <main class="container">
    <article class="grid">
      <div>
        <hgroup>
          <h1>add a document</h1>
        </hgroup>
        <form @submit=addDocument>
          <textarea
            type="textarea"
            name="document"
            v-model="document"
            rows="15"
            cols="50"
            placeholder="document"
            aria-label="document"
            @change=handleTextChange
            required
          />
          <button type="submit" class="contrast">add</button>
        </form>
      </div>
      <div></div>


      <h2>potential secrets so far:</h2>
      <table role="grid">
        <tbody>
          <tr v-for="secret in secrets" :key="secret.toString()">
            <td>{{secret}}</td>
          </tr>
        </tbody>
      </table>
    </article>

  </main>
</template>

<script>
  import axios from 'axios';

  export default {
    async mounted() {
      await this.getHashes();
    },
    methods: {
      async getHashes() {
        try {
          let resp = await axios.get(`company/${this.companyId}/hashes`, {
            headers: { Authorization: `Bearer ${localStorage.token}` }
          });
          this.hashes = resp.data.message;
          // eslint-disable-next-line
         this.hashes.p = BigInt(this.hashes.p);
          // eslint-disable-next-line
         this.hashes.q = BigInt(this.hashes.q);

        } catch (error) {
          console.log(error);
          this.$router.push({
            name : "error",
            query: { error: JSON.stringify(error.response.data.message) },
          });
        }
      },
      async addDocument(event) {
        event.preventDefault();
        let companyId = this.$route.params["companyId"]
        try {
          await axios.put(`/company/${this.companyId}/doc`, {
            text: this.document,
          });


          this.$router.push({name: 'company', params: { companyId }});
        } catch (error) {
          console.log(error);
          this.$router.push({
            name : "error",
            query: { error: JSON.stringify(error.response.data.message) },
          });
        }
      },

      async handleTextChange() {
        // eslint-disable-next-line
        function powmod(base, exp, mod) {
          let res = 1n;
          while (exp != 0n) {
            if (exp % 2n == 1n) {
              res = (res * base) % mod;
            }
            base = (base * base) % mod;
            exp /= 2n;
          }
          return res;
        }


        const textEncoder = new TextEncoder();

        let text = textEncoder.encode(event.target.value);

        let hashes = this.hashes;

        let hashesByLength = new Map();
        let prefixHashes = [];


        let h = 0n;

        prefixHashes.push(h);
        for(let i = 0n; i < text.length; i++) {
          h = (h * hashes.q)
          // eslint-disable-next-line
          h = (h + BigInt(text[i])) % hashes.p
          prefixHashes.push(h)
        }

        for(let i = 0n; i < hashes.hashes.length; i++) {
          // eslint-disable-next-line
          let len = BigInt(hashes.hashes[i].len);
          if (hashesByLength.get(len) == null) {
            hashesByLength.set(len, []);
          }
          hashesByLength.get(len).push(hashes.hashes[i].hash);
        }

        this.secrets = []
        for(const [l, hashList] of hashesByLength) {

          // eslint-disable-next-line
          for(let i = 0n; i < BigInt(text.length) - l + 1n; i++) {
            let thisHash = (hashes.p + prefixHashes[i+l] - (prefixHashes[i] * powmod(hashes.q, l, hashes.p) % hashes.p)) % hashes.p;
            for(const hash of hashList) {
              if (hash == thisHash) {
                this.secrets.push(event.target.value.slice(Number(i), Number(i + l)))
              }
            }
          }
        }

      },
    },
    data: function () {
      return {
        secrets: [],
        secretHashes: [],
        document: null,
        companyId: this.$route.params["companyId"],
      };
    },

  }
</script>
