<template>
  <table role="grid">
    <thead>
      <tr>
        <th scope="col">id</th>
        <th scope="col">name</th>
        <th scope="col">login</th>
        <th scope="col">view company</th>
        <th scope="col">publish document</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="company in companies" :key="company.id">
        <th scope="row">{{company.id}}</th>
        <td>{{company.name}}</td>
        <td>{{company.login}}</td>
        <td><router-link v-if="!loggedIn" :to="{
          name: 'company',
          params: {
            companyId : company.id,
          }        
        }">view</router-link></td>


        <td><router-link v-if="!loggedIn" :to="{
          name: 'putDocument',
          params: {
            companyId : company.id,
          }
        }">publish</router-link></td>
      </tr>
    </tbody>
    </table>

</template>

<script>
  import axios from 'axios';

  export default {
    async mounted() {
      await this.getCompanies();
    },
    methods: {
      async getCompanies() {
        try {
          let resp = await axios.get('companies?offset=0&limit=100');

          this.companies = resp.data.message.companies;

        } catch (error) {
          console.log(error);
          this.$router.push({
            name : "error",
            query: { error: JSON.stringify(error.response.data.message) },
          });
        }
      },
    },
    data: function () {
      return {
        companies: [],
      };
    },

  }
</script>
