<template>
  <main class="container">
    <article class="grid">
      <div>
        <h1>{{document.id}}</h1>
        <p>
          {{document.text}}
        </p>
        </div>
    </article>
  </main>
</template>

<script>
  import axios from 'axios';

  export default {
    async mounted() {
      await this.getCompany();
    },
    methods: {
      async getDocument() {
        try {
          let resp = await axios.get(`doc/${this.documentId}`, {headers: {Authorization: `Bearer ${localStorage.token}`}});

          this.document = resp.data.message;

        } catch (error) {
          console.log(error);
          this.$router.push({
            name : "error",
            query: { error: JSON.stringify(error.response.data.message) },
          });
        }
      },
    },
    data: function () {
      return {
        document: [],
        documentId: this.$route.params["documentId"],
      };
    },

  }
</script>
