<template>

  {{company.id}}
  <h1>
    {{company.name}}
  </h1>

  <h2>
    {{company.login}}
  </h2>

  <h2>
    documents:
  </h2>
  <table role="grid">
    <thead>
      <tr>
        <th scope="col">id</th>
        <th>sanitized</th>
        <th>raw</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="documentId in company.documents" :key="documentId.toString()">
        <th scope="row">{{documentId}}</th>


        <td><router-link :to="{
          name: 'sanitized',
          params: {
            documentId : documentId,
          }        
        }">view</router-link></td>

        <td><router-link :to="{
          name: 'document',
          params: {
            documentId : documentId,
          }        
        }">view</router-link></td>
      </tr>
    </tbody>
    </table>

  <router-link :to="{
          name: 'putDocument',
          params: {
            companyId: company.id + 1 - 1,
          }
        }" role="button">+</router-link>
  <h2>secrets:</h2>
  <table role="grid">
    <thead>
      <tr>
        <th scope="col">id</th>
        <th>secret</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="secret in company.secrets" :key="secret.id">
        <th scope="row">{{secret.id}}</th>
        <td>{{secret.secret}}</td>

      </tr>
    </tbody>
    </table>
  
    <li><router-link to="/me/secret" role="button">+</router-link></li>

</template>

<script>
  import axios from 'axios';

  export default {
    async mounted() {
      await this.getCompany();
    },
    methods: {
      async getCompany() {
        try {
          let resp = await axios.get('/me', {headers: {Authorization: `Bearer ${localStorage.token}`}});

          this.company = resp.data.message;

        } catch (error) {
          console.log(error);
          this.$router.push({
            name : "error",
            query: { error: JSON.stringify(error.response.data.message) },
          });
        }
      },
    },
    data: function () {
      return {
        company: {},
      };
    },

  }
</script>
