import { createRouter, createWebHashHistory } from 'vue-router'
import IndexView from '@/views/IndexView.vue'
import RegisterView from '@/views/RegisterView.vue'
import LoginView from '@/views/LoginView.vue'
import ErrorView from '@/views/ErrorView.vue'
import CompaniesView from '@/views/CompaniesView.vue'
import CompanyView from '@/views/CompanyView.vue'
import SanitizedView from '@/views/SanitizedView.vue'
import DocumentView from '@/views/DocumentView.vue'
import PutSecretView from '@/views/PutSecretView.vue'
import PutDocumentView from '@/views/PutDocumentView.vue'
import MeView from '@/views/MeView.vue'

const routes = [
  {
    path: '/',
    name: 'index',
    component: IndexView
  },
  {
    path: '/register',
    name: 'register',
    component: RegisterView
  },
  {
    path: '/login',
    name: 'login',
    component: LoginView
  },
  {
    path: '/error',
    name: 'error',
    component: ErrorView
  },
  {
    path: '/companies',
    name: 'companies',
    component: CompaniesView
  },
  {
    path: '/company/:companyId',
    name: 'company',
    component: CompanyView
  },
  {
    path: '/doc/:documentId/sanitized',
    name: 'sanitized',
    component: SanitizedView
  },
  {
    path: '/doc/:documentId',
    name: 'document',
    component: DocumentView
  },
  {
    path: '/me',
    name: 'me',
    component: MeView
  },
  {
    path: '/me/secret',
    name: 'putSecret',
    component: PutSecretView
  },
  {
    path: '/company/:companyId/doc',
    name: 'putDocument',
    component: PutDocumentView
  },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
